function getCForDisplay(val, lang, c) {
    if (lang === 'English') {
        if (val) return c.toLowerCase()
        else return c
    }
    else
        if (val)
            switch (c.toLowerCase()) {
                case 'nature': return ''
                case 'geography': return ''
                case 'animals': return ' '
                case 'personalities': return ''
                case 'movies': return ''
                case 'medicine': return ''
                case 'food': return ''
                case 'sports': return ''
                case 'music': return ''
                case 'science': return ''
                case 'technology': return ''
            }
        else {

            switch (c.slice(0, 1).toUpperCase() + c.slice(1)) {
                case 'Argentina': return ''
                case 'Australia': return ''
                case 'Austria': return ''
                case 'Belgium': return ''
                case 'Brazil': return ''
                case 'Canada': return ''
                case 'China': return ''
                case 'Colombia': return ''
                case 'Denmark': return ''
                case 'England': return ''
                case 'Ethiopia': return ''
                case 'Finland': return ''
                case 'France': return ''
                case 'Germany': return ''
                case 'India': return ''
                case 'Israel': return ''
                case 'Italy': return ''
                case 'Japan': return ''
                case 'Mexico': return ''
                case 'Morocco': return ''
                case 'Philippines': return ''
                case 'Poland': return ''
                case 'Portugal': return ''
                case 'Romania': return ''
                case 'Russia': return ''
                case 'Spain': return ''
                case 'Sweden': return ''
                case 'Switzerland': return ''
                case 'Thailand': return ''
                case 'Ukraine': return ''
                case 'USA': return ' '
            }
        }
}

function tranCountry(lang, country) {
    console.log(country);
    var arr = [
        ['Argentina', 'Australia', 'Austria', 'Belgium', 'Brazil', 'Canada', 'China', 'Colombia', 'Denmark', 'England', 'Ethiopia', 'Finland', 'France', 'Germany', 'India', 'Israel', 'Italy', 'Japan', 'Mexico', 'Morocco', 'Philippines', 'Poland', 'Portugal', 'Romania', 'Russia', 'Spain', 'Sweden', 'Switzerland', 'Thailand', 'Ukraine', 'USA'],
        ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ' ']
    ]
    var currCountry = arr[lang === 'English' ? 0 : 1][(arr[lang === 'English' ? 1 : 0]).findIndex(c => c === country)]
    return currCountry
}

